<template>
  <div class="hello">
    <section class="hero is-medium has-bg-img">
      <div class="hero-body has-text-black">
        <div class="container has-text-black">
          <h1 class="title is-size-1-desktop">やど祭模擬店リスト</h1>
        </div>
      </div>
    </section>
    <section class="section">
      <div class="container">
        <h3 class="title">STORE LIST</h3>
        <c-store-table></c-store-table>
      </div>
    </section>
    <section class="section">
      <div class="container">
        <h3 class="title">
          ABOUT
        </h3>
        <p>
          このサイトは第44回筑波大学宿舎祭やどかり祭の模擬店の<strong>非公式</strong>ポータルサイトです。
        </p>
        <p>
          掲載されているデータはTwitterを元にすだめ(<a href="https://twitter.com/ITF_sudame">ITF_sudame</a>)が解析したものです。
        </p>
        <p>
          掲載データはゴールデンウィークの気分とレポートの消化次第で順次拡大するかもしれませんし、しないかもしれません。
        </p>
      </div>
    </section>
    <section class="section">
      <div class="container">
        <h3 class="title">FEEDBACK</h3>
        <p>
          ご意見・ご感想は下記フォームにて受け付けております。
        </p>
        <p>
          掲載・訂正・削除のご依頼も下記からお願いいたします。
        </p>
        <c-opinion-form></c-opinion-form>
      </div>
    </section>
  </div>
</template>

<script>
import StoreTable from './StoresTable';
import OpinionForm from './OpinionForm';

export default {
  components: {
    'c-store-table': StoreTable,
    'c-opinion-form': OpinionForm,
  },
  name: 'HelloWorld',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lnag="scss" scoped>
.has-bg-img {
  background: url('../assets/hero-bg.jpg') center center;
  background-size: cover;
}

h1,
h2 {
  font-weight: normal;
}
</style>



