<template>
  <b-table :data="data" default-sort-direction="asc" default-sort="id">
      <template slot-scope="props">
          <b-table-column field="store_id" label="店名" sortable>
              {{ props.row.store_name }}
          </b-table-column>
          <b-table-column field="group_id" label="団体" sortable>
              {{ props.row.group_name }}
          </b-table-column>
          <b-table-column field="product_id" label="商品" sortable>
              {{ props.row.product }}
          </b-table-column>
          <b-table-column field="Twitter" label="Twitter" sortable>
              <a :href="'https://twitter.com/' + props.row.twitter" class="button is-info is-small is-outlined">@{{ props.row.twitter }}</a>
          </b-table-column>
      </template>
  </b-table>
</template>

<script>
const data = require('../assets/storeData.json');

export default {
  data() {
    return {
      data,
    };
  },
};
</script>

<style>
</style>



