<template>
    <div>
        <!-- <b-field label="メールアドレス">
            <b-input
                v-model="email"
                class="form-item email-form"
                type="email"
                placeholder="メールアドレス"></b-input>
        </b-field> -->
        <b-field label="本文">
            <b-input v-model="opinion"
                class="form-item"
                type="textarea"
                placeholder="ご意見"></b-input>
        </b-field>
        <button class="button form-item is-danger is-outlined" @click="sendOpinion">送信</button>
    </div>
</template>

<script>
export default {
  data() {
    return {
      email: '',
      opinion: '',
    };
  },
  methods: {
    sendOpinion() {
      fetch(
        `https://script.google.com/macros/s/AKfycbzxkK33wA48FdDzOPvybPSDEb9FBLUwE2xbA56-ZxNXeqyYbxPF/exec?opinion=${
          this.opinion
        }&email=${this.email}`,
        {
          mode: 'no-cors',
        },
      ).then(() => {
        this.$toast.open({
          duration: 3000,
          message: '送信しました。',
          position: 'is-bottom',
          type: 'is-success',
        });
      });
    },
  },
};
</script>

<style lang='scss' scoped>
.form-item {
  margin: 0.5rem 0;
  max-width: 500px;
}
</style>



